/* 네비게이션 바 - 메뉴 스타일 */
.navbar-menu {
    display: flex;
    justify-content: flex-start;
    gap: 30px;
    list-style-type: none;
    width: 100%;
  }
  
  .navbar-menu li {
    padding: 5px 15px;
  }
  
  .navbar-menu li a {
    text-decoration: none;
    color: white;
    font-weight: bold;
    font-size: 1.1em; /* 기본 폰트 크기 */
    padding: 5px 15px;
    transition: background-color 0.3s ease;
  }
  
  .navbar-menu li a:hover {
    background-color: #8f94fb;
    border-radius: 5px;
  }
  
  /* 모바일 화면에서 메뉴 폰트 크기 줄이기 */
  @media (max-width: 768px) {
    .navbar-menu {
      flex-direction: column; /* 모바일에서는 세로로 나열 */
      gap: 10px;
      justify-content: flex-start;
      font-size: 0.7rem; /* 모바일 메뉴 폰트 크기 줄이기 */
    }
  }
  