.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* 기본 스타일 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'STUNNING-Bd', sans-serif; /* 'STUNNING-Bd' 폰트 적용 */
}

/* OTF 폰트 적용 */
@font-face {
  font-family: 'STUNNING-Bd';
  src: url('/public/font/STUNNINGOTF.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

body {
  background-color: #f4f4f4;
  color: #333;
  font-size: 14px;
  line-height: 1.6;
  overflow-x: hidden;
  padding-left: 15px;
  padding-right: 15px;
  transition: all 0.3s ease;
}

/* 앱 전체 컨테이너 */
.app {
  font-family: 'STUNNING-Bd', sans-serif;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

/* 네비게이션 바 - 메뉴 왼쪽 정렬 */
.navbar {
  background-color: #4e54c8;
  color: white;
  padding: 15px;
  position: sticky;
  top: 0;
  z-index: 10;
  width: 100%;
}

.navbar-menu {
  display: flex;
  justify-content: flex-start;
  gap: 30px;
  list-style-type: none;
  width: 100%;
}

.navbar-menu li {
  padding: 5px 15px;
}

.navbar-menu li a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 1.1em; /* 메뉴 항목 글씨 크기 */
  padding: 5px 15px;
  transition: background-color 0.3s ease;
}

.navbar-menu li a:hover {
  background-color: #8f94fb;
  border-radius: 5px;
}

/* 부드러운 스크롤 이동 */
html {
  scroll-behavior: smooth; /* 스크롤 시 부드럽게 이동 */
}

/* 소개 섹션 */
.about-section {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-direction: column;
  padding: 40px 0;
  min-height: auto; /* 모바일 화면에서 전체 화면을 차지하도록 설정 */
}

.profile-container {
  display: flex;
  justify-content: flex-start; /* 왼쪽 정렬 */
  align-items: center;
  gap: 20px;
  flex-direction: row;
}

.profile-image {
  width: 100px;
  height: 100px;
  border-radius: 50%; /* 원형 프로필 이미지 */
  object-fit: cover;
}

.about-text {
  max-width: 500px;
  text-align: left; /* 소개글 왼쪽 정렬 */
}

.about-text h1 {
  font-size: 1.6em;
  margin-bottom: 10px;
}

.about-text p {
  font-size: 0.95em;
}

/* 자격증 섹션 */
.certifications-section {
  margin-bottom: 30px; /* 자격증과 프로젝트 간격을 줄이기 위한 margin */
  padding: 20px 0; /* 자격증과 프로젝트 간격을 줄이기 위한 padding */
}

/* 프로젝트 섹션 */
.projects-section {
  margin-bottom: 60px;
}

.projects-container {
  padding: 20px;
}

.filter-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 30px;
}

.filter-button {
  background-color: #4e54c8;
  color: white;
  padding: 8px 18px;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.filter-button:hover {
  background-color: #8f94fb;
  transform: scale(1.1);
}

.projects-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.project-card {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.project-card h2 {
  font-size: 1.1em;
  margin-bottom: 10px;
}

.project-card p {
  font-size: 0.95em;
  margin: 10px 0;
}

/* 기술 스택 */
.tech-stack {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
  justify-content: center;
}

.tech-stack-item {
  background-color: #f0f0f0;
  color: #333;
  padding: 8px 15px;
  border-radius: 12px;
  font-size: 0.9em;
  transition: background-color 0.3s ease;
}

.tech-stack-item:hover {
  background-color: #4e54c8;
  color: white;
}

/* 연락처 섹션 */
.contact-section {
  text-align: center;
  margin-bottom: 40px;
}

.contact-section a {
  color: #4e54c8;
  text-decoration: none;
}

.contact-section a:hover {
  text-decoration: underline;
}

/* 반응형 디자인 */
@media (max-width: 768px) {
  .navbar-menu {
    flex-direction: column; /* 모바일에서는 세로로 나열 */
    gap: 10px;
    justify-content: flex-start;
    font-size: 0.7rem; /* 모바일 메뉴 폰트 크기 줄이기 */
  }

  .about-section, .projects-section, .contact-section {
    text-align: center;
  }

  .about-section {
    min-height: auto; /* 모바일에서 소개글이 잘리지 않도록 수정 */
    padding: 20px;
  }

  .profile-container {
    flex-direction: column; /* 모바일에서 세로로 나열 */
  }
}

@media (max-width: 480px) {
  .navbar-menu {
    flex-direction: column; /* 모바일에서도 메뉴를 세로로 */
    gap: 10px;
    font-size: 0.7rem; /* 모바일 메뉴 폰트 크기 줄이기 */
  }
}
