/* 기본 버튼 스타일 */
.scroll-to-top {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: #4e54c8;
    color: white;
    border: none;
    padding: 15px;
    border-radius: 50%;
    cursor: pointer;
    display: none; /* 처음에는 버튼 숨김 */
  }
  
  /* 버튼이 화면에 나타날 때 */
  .scroll-to-top.visible {
    display: block;
  }
  
  /* 반응형 디자인: 모바일 화면에서도 버튼 위치 조정 */
  @media (max-width: 768px) {
    .scroll-to-top {
      bottom: 10px;
      right: 10px;
    }
  }
  