/* 기본적인 프로젝트 리스트 스타일 */
.projects-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
  }
  
  .project-card {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .project-card h2 {
    font-size: 1.2em;
    margin-bottom: 10px;
  }
  
  .project-card p {
    margin: 10px 0;
  }
  
  .tech-stack {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
  }
  
  .tech-stack-item {
    background-color: #f0f0f0;
    color: #333;
    padding: 8px 15px;
    border-radius: 12px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    font-size: 1em;
    transition: background-color 0.3s ease;
  }
  
  .tech-stack-item:hover {
    background-color: #4e54c8;
    color: white;
  }
  
  /* 반응형: 모바일 화면에 맞게 프로젝트 카드 크기 조정 */
  @media (max-width: 768px) {
    .projects-list {
      grid-template-columns: 1fr;  /* 모바일 화면에서는 1열로 */
    }
  
    .project-card {
      padding: 15px;
    }
  }
  